<template>
	<view>
		<image src="../../static/logo-2.png" mode="widthFix" class="logo"></image>
		<view class="register-container">
			<input type="text" placeholder="输入你的邀请码" class="register-code" maxlength="6" v-model="registerCode" />
			<view class="register-desc">管理员创建员工证账号之后，你可以从你的个人邮箱中获得注册邀请码</view>
			<button class="register-btn" open-type="getUserProfile" @tap="register">执行注册</button>
		</view>
		<u-toast ref="registerWaring"></u-toast>
	</view>
</template>

<script>
	import userApi from '../../network/user/user.js'

	export default {
		data() {
			return {
				//邀请码
				registerCode: ""
			};
		},
		mounted() {

		},
		methods: {
			async register() {
				let that = this
				if (that.registerCode == null || that.registerCode.length == 0) {
					this.$refs.registerWaring.show({
						type: 'error',
						icon: false,
						message: '邀请码不能为空'
					})
					return
				}
				//判断是否是六位邀请码
				else if (/^[0-9]{6}$/.test(that.registerCode) == false) {
					this.$refs.registerWaring.show({
						type: 'error',
						icon: false,
						message: '请输入六位邀请码'
					})
					return
				}

				await uni.getUserProfile({
					provider: "weixin",
					desc: "用于完善用户资料",
					lang: "zh_CN",
					success: (res) => this.saveUserInfo(res),
					fail: (fail) => {
						this.$refs.registerWaring.show({
							type: 'error',
							icon: false,
							message: '您拒绝了授权,登录失败'
						})
						return
					}
				})
			},
			//用于登录请求
			async saveUserInfo(e) {
				console.log(e)
				const {
					encryptedData,
					iv,
					rawData,
					signature,
					userInfo
				} = e

				//获取登录的
				const [, {
					code
				}] = await uni.login()

				//调用注册用户方法
				let registerForm = {
					code,
					nickname: userInfo.nickName,
					registerCode: this.registerCode,
					photo: userInfo.avatarUrl
				}
				console.log("用户注册接口", registerForm)
				const {
					permission,
					token
				} = await userApi.register(registerForm);

				//把数据包装到wxuser中
				let wxuser = {
					permission,
					token
				}
				//设置用户权限到本地缓存中
				getApp().globalData.global_setStorageSync("wxuser", wxuser)
				uni.switchTab({
					url: "/pages/index/index"
				})
			}
		}
	};
</script>

<style lang="scss">
	@import "register.scss";
</style>
